.form {
    width: 420px;
    padding-bottom: 30px;

}
.inputs_for_id input, .inputs_for_box input{
    display: block;
    margin-left: 10px;
    width: 300px;
    height: 35px;
    border: 0;
    margin-top: 20px;
    font-size: 20px;
    outline: none;
    text-transform: capitalize;
    border-bottom: 2px solid #242b2e;
}
.inputs_for_box input {
    float: left;
}
.inputs_for_box button {
    background: #717171;
    color: white;
    font-weight: bold;
    border: 0;
    height: 35px;
    margin-top: 24px;
    width: 50px;
    cursor: pointer;
    transition: all 0.3s;
    outline: none;
    float: left;
}
.inputs_for_box button:hover, .addJustInput:hover {
    opacity: 0.7;
}
.inputs_for_box button > img {
    width: 30px;
    margin-top: 1px;
}
.inputs_for_box button:nth-child(3) {
    margin-left: 5px;
    float: left;
}
.inputs_for_box div.active {
    transition: all 1s;
    transform: scale(0.5);
    opacity: 0;
}
.addJustInput {
    width: 305px;
    margin-left: 10px;
    margin-top: 10px;
    height: 35px;
    background: #717171;
    border: 0;
    color: white;
    font-family: monospace;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 3px;
    cursor: pointer;
    transition: all 0.3s;
    outline: none;
}
.product_info {
    width: 450px;
    min-height: 100px;
    margin-top: 20px;
    margin-left: 10px;
    background: #818181;
}
.btns {
    display: flex;
    margin-left: 10px;
    margin-top: 10px;
    justify-content: space-between;
    width: 305px;
}
.btns > button {
    width: 140px;
    height: 30px;
    border: 0;
    outline: none;
    background: #717171;
    color: white;
    font-size: 15px;
    cursor: pointer;
    text-transform: uppercase;
    transition: all 0.3s;
}
.btns > button:hover {
    opacity: 0.7;
}
.kronn_block {
    display: flex;
    margin-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
}
.kronn_block img{
    width: 200px;
}
.title_kronn p {
    font-weight: bold;
    margin-left: 10px;
    font-size: 18px;
    color: white;
}
.total_price p {
    font-size: 18px;
    margin-left: 10px;
    padding-top: 60px;
}